  position: absolute;
  right: calc(-1 * var(--rm-panel-width));
  background: var(--rm-color-background);
  color: var(--rm-color-on-background);
  transition: right var(--rm-panel-transition-speed);
  overflow-y: scroll;
  padding-bottom: var(--rm-space-large);
}

.panel--active {
  right: 0;
  box-shadow: var(--rm-shadow-x-large);
}

.panel__close {
  float: right;
  cursor: pointer;
  display: flex;
  padding: var(--rm-space-medium);
  border-radius: 100%;
}
.panel__close:hover {
  background: var(--rm-color-neutral-plus-3);
}

#panel-content {
  padding-top: var(--rm-space-large);
  padding-bottom: var(--rm-space-large);
}

.modal {
  --rm-modal-width: 564px;
  --rm-modal-transition-speed: 300ms;
  visibility: hidden;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: var(--rm-z-index-dialog);
  overflow: hidden;
  /*Prevent Chrome on Windows from adding a focus outline. For details, see*/
  /*https://github.com/twbs/bootstrap/pull/10951.*/
  outline: 0;
  /*We deliberately don't use `-webkit-overflow-scrolling: touch;` due to a
  gnarly iOS Safari bug: https://bugs.webkit.org/show_bug.cgi?id=158342
  See also https://github.com/twbs/bootstrap/issues/17695*/
}

.modal__backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: var(--rm-z-index-dialog-backdrop);
  background: black;
  visibility: hidden;
  opacity: 0;
  transition: all var(--rm-modal-transition-speed);
}

.modal__content {
  max-height: 500px;
  width: var(--rm-modal-width);
  padding: var(--rm-space-large);
  background: var(--rm-color-background);
  color: var(--rm-color-on-background);
  border-radius: var(--rm-radius-large);
  z-index: var(--rm-z-index-dialog-content);
  transform: scale(0.7);
  opacity: 0;
  overflow-y: auto;
  transition: all var(--rm-modal-transition-speed);
}

.modal--active {
  display: flex;
  visibility: visible;
}
.modal--active .modal__content {
  transform: scale(1);
  opacity: 1;
}
.modal--active .modal__backdrop {
  visibility: visible;
  opacity: 0.5;
}

.modal__body {
  overflow-y: auto;
}

.modal__close {
  float: right;
  cursor: pointer;
  color: var(--rm-color-neutral-minus-3);
}
.modal__close:hover:not(:disabled, .btn--disabled) {
  color: var(--rm-color-neutral-minus-3);
}

:root {
  --rm-color-primary-h: 234;
  --rm-color-primary-s: 32%;
  --rm-color-primary-l: 52%;
  --rm-font-family: "Poppins", sans-serif;
  --rm-card-padding: var(--rm-space-large);
}

/* footer */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-content: center;
  background-color: var(--rm-color-primary-base);
  color: var(--rm-color-primary-on-base);
  padding: var(--rm-space-large);
}

a.nav-bar__brand {
  text-transform: uppercase;
  font-size: var(--rm-font-2x-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-primary-on-base);
}

a.nav-bar__link {
  text-transform: uppercase;
  font-size: var(--rm-font-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-primary-on-base);
  border-bottom: 1rem solid var(--rm-color-primary-base);
}

a.nav-bar__link:hover {
  border-bottom: 1rem solid var(--rm-color-primary-on-base);
}

a.nav-bar__link--active {
  border-bottom: 1rem solid var(--rm-color-primary-on-base);
}

.player-hand {
  display: flex;
  justify-content: center;
  overflow-y: scroll;
  scroll-behavior: smooth;
  outline: solid;
  padding-bottom: 3rem;
}

.game-screen {
  display: flex;
  position: relative;
  justify-content: space-between;
  height: 70%;
}

.deck-card {
  width: 150px;
}